            about1:'Международная Выставка инструментов для ремонта автомобилей, сервисного оборудования, запасных частей, средств обслуживания и аксессуаров - KAZAUTOEXPO2022',
            about2:'- первая выставка автоинструментов,компанентов,  запчастей и аксессуаров. Ведущая торговая выставка запчастей, оборудования, компонентов, аксессуаров, инструментов и услуг для автомобилей, грузовиков и автобусов будет знакомить Вас с  ведущими производителями. Выставка объединяет всех производителей автоиндустрии. {break} {break} Международная выставка «KAZAUTOEXPO2022» - одна из крупных специализированных мероприятии в Казахстане. {break}•  общая экспозиционная площадь – около 1500 кв. метров; {break} •  участники – из 10 стран мира. {break} {break} Ведущая специализированная выставка в среднеазиатском регионе входит в число самых приоритетных мероприятий Казахстана и поддерживается Ассоциация казахстанского автобизнеса (АКАБ), Палатой предпринимателей г. Алматы, Ассоциацией Торговых и Промышленных предприятий РК. {break}{break} На участие приглашаются как известные торговые бренды, так и новые торговые марки, которые еще не известны на казахстанском рынке. ',
            about3:'Профиль Участников',
