            forms1:'С выставочным стендом внутри павильона (Стандартный оборудованный стенд):',
            forms2:'Включает закрытую выставочную площадь, стеновые панели в сборе, фризовую надпись, ковровое покрытие, мебель и освещение в зависимости от квадратуры. С комплектацией типовых стендов вы можете ознакомиться в техническом руководстве выставки.',
            forms3:'Необорудованная выставочная площадь:',
            forms4:'Включает условно обозначенную площадь пола, предназначенную для экспонирования образцов или оформление стенда силами компании-участника. В случае, если оформлением стенда занимается компания – застройщик, ей необходимо пройти обязательную аккредитацию в месте проведения мероприятия, что продиктовано мерами общественной безопасности. В стоимость данного места не включены ни стены, ни электричество, ни ковровое покрытие, ни мебель. В этом случае нужно будет заказать у организаторов подключение электричества.',
            forms5:'Заочное участие:',
            forms6:'Предполагает одну или все перечисленные формы: размещение информации о компании в официальном каталоге выставки / рекламных конструкций / работу одного промоутера / распространение рекламно-полиграфической продукции.',
            forms7:'Спонсорство / Партнерство:',
            forms8:'Статус спонсора выставки ассоциируется с успехом, солидностью, особым положением и широкими возможностями компании. Используйте возможности наших выставок для поддержания статуса Вашей успешности, расширения границ бизнеса и привлечения максимального внимания целевой аудитории к Вашему бренду и осуществления новых продаж. Мы предлагаем различные спонсорские и партнерские пакеты. Список опций рассматривается индивидуально, мы всегда готовы обсудить вопрос о специальных возможностях в рамках спонсорства выставки, которые будут соответствовать Вашему бюджету, потребностям и целям.<br> Для получения дополнительной информации и специального предложения, пожалуйста, обращайтесь к организаторам мероприятия.',

