            opening11:'Письмо (или его копия)',
            opening12:'Проезд всего транспорта к павильону Event Space будет осуществляться через въезд со стороны ул. Сейфулина и Тимирязева.<br> По всем вопросам, связанным  с доставкой экспонатов на выставку  Вы можете обращаться по тел.: + 7 771 506 85 32',

            rules:'Все лица, принявшие приглашение организатора посетить выставку, а также зарегистрировавшиеся в качестве посетителя или купившие кассовый билет, соглашаются с данными Правилами и обязуются соблюдать их. <br> 1. Основанием для входа на выставку в течение всего периода работы выставки является бейдж.  <br> 2. Заполнение анкеты посетителя при обмене на бедж является обязательным! <br> Бланки формы находятся на столах рядом со стойкой регистрации. <br> 3. В случае утраты беджа можно обратиться на стойку регистрации для его восстановления. <br> 4. Передача или продажа беджей посетителей любым третьим лицам запрещена. Организатор выставки вправе проверить соответствие имени, указанного в бедже, с именем посетителя, для чего Организатор может попросить посетителя Выставки предъявить удостоверяющий личность документ. <br> 5. На территории выставки посетителям не разрешается осуществлять торговлю, распространять любую рекламно-информационную продукцию, проводить презентации, демонстрации, агитации и любые другие подобные мероприятия и действия без письменного согласия организатора. <br>',
