            about1:'Автокөлік жөндеу құралдарының, сервистік жабдықтың, қосалқы бөлшектердің, сервистік құралдар мен аксессуарлардың халықаралық көрмесі - KAZAUTOEXPO2022. ',
            about2:'-  автокөлік құралдары мен бөлшектерінің, қосалқы бөлшектердің және аксессуарлардың бірінші көрмесі. Жеңіл автомобильдерге, жүк көліктеріне және автобустарға арналған бөлшектер, жабдықтар, жинақтауыштар, керек-жарақтар, құралдар мен көлік қызметтеріне арналған жетекші сауда көрмесі сізді жетекші өндірушілермен таныстырады. Көрме автомобиль өнеркәсібінің барлық өндірушілерін біріктіреді. {break}{break} Халықаралық «KAZAUTOEXPO2022» көрмесі Қазақстандағы ірі мамандандырылған шаралардың бірі болып табылады. {break} • экспозицияның жалпы ауданы – шамамен 1500 ш.м. метр; {break} • қатысушылар – әлемнің 10 елінен.  {break}{break} Орталық Азия аймағындағы жетекші мамандандырылған көрме Қазақстандағы ең басты оқиғалардың бірі болып табылады және оны Қазақстан Автобизнес қауымдастығы (AKAB), Алматы қаласы Кәсіпкерлер палатасы, Қазақстан Республикасының Сауда-өнеркәсіптік кәсіпорындар қауымдастығы қолдап отыр. {break} {break} Көрмеге қатысуға танымал сауда брендтері, Қазақстандық нарықта әлі белгісіз жаңа сауда белгілері де шақырылады. ',
            about3:'Қатысушылардың Профилі:',
