            forms4:'Үлгілерді көрсетуге немесе қатысушы компанияның күшімен стендті безендіруге арналған шартты түрде белгіленген еден алаңын қамтиды. Егер стендті жобалаумен құрылыс салушы компания айналысатын болса, оған іс-шара өткізілетін жерде міндетті аккредиттеуден өту қажет, бұл қоғамдық қауіпсіздік шараларымен түсіндіріледі. Бұл жердің құнына қабырғалар да, электр де, кілем де, жиһаз да кірмейді. Бұл жағдайда ұйымдастырушылардан электр қуатын қосуға тапсырыс беру керек.',
            forms5:'Сырттай қатысу:',
            forms6:'Бұл бір немесе барлық көрсетілген нысандарды қамтиды: Компания туралы ақпаратты көрменің / жарнамалық құрылымдардың ресми каталогына орналастыру / бір промоутердің жұмысы / жарнамалық және баспа өнімдерін тарату.',
            forms7:'Демеушілік / Серіктестік:',
            forms8:'Көрме демеушісінің мәртебесі компанияның табысымен, беріктігімен, ерекше жағдайымен және кең мүмкіндіктерімен байланыстырылады. Сіздің жетістіктеріңіздің мәртебесін сақтау, Бизнестің шекарасын кеңейту және мақсатты аудиторияның назарын сіздің брендіңізге аудару және жаңа сатылымдар жасау үшін біздің көрмелеріміздің мүмкіндіктерін пайдаланыңыз. Біз әр түрлі демеушілік және серіктестік пакеттерді ұсынамыз. Опциялар тізімі жеке қарастырылады, біз сіздің бюджетіңізге, қажеттіліктеріңізге және мақсаттарыңызға сәйкес келетін көрме демеушілігі аясында арнайы мүмкіндіктер туралы мәселені талқылауға әрқашан дайынбыз.<br> қосымша ақпарат пен арнайы ұсыныс алу үшін шараны ұйымдастырушыларға хабарласыңыз.',

