            participants2:'Көрмені ұйымдастырушы Көлік және кеден қызметтерін көрсетпейді, экспонаттар ретінде көрмеге қатысушыларға жөнелтілетін жүкті алушы болып табылмайды және ол үшін жауапты болмайды. <br> <br> көрме жұмысы кезінде жабдықтар мен экспонаттарды әкелуге/әкетуге тыйым салынады.',
     
