            stand3:'Стандартты стенд дегеніміз не?',
            stand4:'Стандартты стенд алюминий пурлиндер мен ақ қабырға панельдері (2,5 м x 1 м) конструкциясы болып табылады. Стандартты стендтерді салу үшін Octanorm конструкторы қолданылады. <br> Стандартты стенд 12 м2 қамтиды: <br>• кілем (сұр) <br>• 1 үстел, 2 орындық <br>• компания атауы бар фассиялық панель (15 таңбадан аспайды) <br>• 3 дана <br>• розетка 220 Вт <br>•қоқыс салатын жәшік <br>',
