            rules:'Ұйымдастырушының көрмеге шақыруын қабылдаған, сондай-ақ келуші ретінде тіркелген немесе кассалық билетті сатып алған барлық тұлғалар осы Ережелермен келісіп, оларды сақтауға міндеттенеді. <br> 1. Көрме жұмысының бүкіл кезеңі ішінде көрмеге кіру үшін бейдж негіз болып табылады. <br> 2. Беджге айырбастау кезінде келушінің сауалнамасын толтыру міндетті болып табылады! <br > нысан бланкілері тіркеу бағанасының жанындағы үстелдерде болады. <br> 3. Бедж жоғалған жағдайда оны қалпына келтіру үшін тіркеу тағанына жүгінуге болады. <br> 4. Келушілердің бейдждерін кез келген үшінші тұлғаға беруге немесе сатуға тыйым салынады. Ұйымдастырушы бейджде көрсетілген есімнің келушінің атымен сәйкестігін тексеруге құқылы, ол үшін Ұйымдастырушы келушіден жеке басын куәландыратын құжатты көрсетуін сұрай алады. <br> 5. Көрме аумағында келушілерге ұйымдастырушының жазбаша келісімінсіз сауда жасауға, кез келген жарнамалық-ақпараттық өнімді таратуға, таныстырулар, демонстрациялар, үгіт жүргізуге және кез келген басқа да осындай іс-шаралар мен іс-әрекеттер жүргізуге тыйым салынады. <br>',

